/**
 * ÏãúÏä§ÌÖú Ï†ÑÏ≤¥ Ïó∞Îèô ÏÉÅÌÉú Ï†êÍ≤Ä Ïú†Ìã∏Î¶¨Ìã∞
 * Frontend ‚Üí Backend ‚Üí Database Ïó∞Í≤∞ ÌÖåÏä§Ìä∏
 */

import axios from 'axios';
import { API_BASE_URL } from '../config/api';

interface HealthCheckResult {
  component: string;
  status: 'healthy' | 'degraded' | 'error';
  responseTime?: number;
  message?: string;
  details?: unknown;
}

interface HealthReport {
  timestamp: string;
  overallStatus: 'healthy' | 'degraded' | 'error';
  results: HealthCheckResult[];
  summary: { total: number; healthy: number; degraded: number; error: number };
}

class SystemHealthChecker {
  private results: HealthCheckResult[] = [];

  // 1. ÌîÑÎ°†Ìä∏ÏóîÎìú ÏÉÅÌÉú Ï†êÍ≤Ä
  async checkFrontend(): Promise<HealthCheckResult> {
    const startTime = Date.now();
    try {
      const buildInfo = {
        version: process.env.REACT_APP_VERSION || '1.0.0',
        buildTime: process.env.REACT_APP_BUILD_TIME || new Date().toISOString(),
        environment: process.env.NODE_ENV || 'production'
      };

      return {
        component: 'Frontend (React)',
        status: 'healthy',
        responseTime: Date.now() - startTime,
        message: 'React application is running',
        details: buildInfo
      };
    } catch (error) {
      return {
        component: 'Frontend (React)',
        status: 'error',
        message: `Frontend check failed: ${error}`
      };
    }
  }

  // 2. Î∞±ÏóîÎìú ÏÑúÎ≤Ñ ÏÉÅÌÉú Ï†êÍ≤Ä
  async checkBackendServer(): Promise<HealthCheckResult> {
    const startTime = Date.now();
    try {
      const response = await axios.get(`${API_BASE_URL}/health/`, {
        timeout: 5000,
        validateStatus: () => true
      });

      if (response.status === 200) {
        return {
          component: 'Backend Server (Django)',
          status: 'healthy',
          responseTime: Date.now() - startTime,
          message: 'Django server is responding',
          details: response.data
        };
      } else {
        return {
          component: 'Backend Server (Django)',
          status: 'degraded',
          responseTime: Date.now() - startTime,
          message: `Server returned status ${response.status}`,
          details: response.data
        };
      }
    } catch (error: unknown) {
      const errMsg = error instanceof Error ? error.message : String(error);
      return {
        component: 'Backend Server (Django)',
        status: 'error',
        message: `Cannot connect to backend: ${errMsg}`,
        details: {
          url: API_BASE_URL,
          error: errMsg
        }
      };
    }
  }

  // 3. Îç∞Ïù¥ÌÑ∞Î≤†Ïù¥Ïä§ Ïó∞Í≤∞ Ï†êÍ≤Ä
  async checkDatabase(): Promise<HealthCheckResult> {
    const startTime = Date.now();
    try {
      const response = await axios.get(`${API_BASE_URL}/db-status/`, {
        timeout: 5000,
        validateStatus: () => true
      });

      if (response.status === 200 && response.data) {
        return {
          component: 'Database (PostgreSQL)',
          status: 'healthy',
          responseTime: Date.now() - startTime,
          message: 'Database connection is active',
          details: response.data
        };
      } else {
        return {
          component: 'Database (PostgreSQL)',
          status: 'degraded',
          responseTime: Date.now() - startTime,
          message: 'Database status check returned unexpected response',
          details: response.data
        };
      }
    } catch (error: unknown) {
      // ÎåÄÏ≤¥ Î∞©Î≤ï: APIÎ•º ÌÜµÌï¥ Í∞ÑÏ†ë ÌôïÏù∏
      try {
        const testResponse = await axios.get(`${API_BASE_URL}/api/service/status/`, {
          timeout: 5000
        });

        if (testResponse.status === 200) {
          return {
            component: 'Database (PostgreSQL)',
            status: 'degraded',
            message: 'Database likely connected (indirect check)',
            responseTime: Date.now() - startTime,
            details: { indirect: true }
          };
        }
      } catch (innerError) {
        // ÏµúÏ¢Ö Ïã§Ìå®
      }

      const errMsg = error instanceof Error ? error.message : String(error);
      return {
        component: 'Database (PostgreSQL)',
        status: 'error',
        message: `Database check failed: ${errMsg}`
      };
    }
  }

  // 4. API ÏóîÎìúÌè¨Ïù∏Ìä∏ Ï†êÍ≤Ä
  async checkAPIEndpoints(): Promise<HealthCheckResult> {
    const startTime = Date.now();
    const endpoints = [
      '/api/service/projects/projects/',
      '/api/service/status/',
      '/api/service/auth/token/verify/'
    ];

    interface EndpointResult { endpoint: string; status: number; responseTime?: number; healthy: boolean; error?: string }
    const results: EndpointResult[] = [];
    let healthyCount = 0;

    for (const endpoint of endpoints) {
      try {
        const endpointStart = Date.now();
        const response = await axios.get(`${API_BASE_URL}${endpoint}`, {
          timeout: 3000,
          validateStatus: () => true,
          headers: {
            'Content-Type': 'application/json'
          }
        });

        const responseTime = Date.now() - endpointStart;
        const isHealthy = response.status < 500;
        if (isHealthy) healthyCount++;

        results.push({
          endpoint,
          status: response.status,
          responseTime,
          healthy: isHealthy
        });
      } catch (error: unknown) {
        const errMsg = error instanceof Error ? error.message : String(error);
        results.push({
          endpoint,
          status: 0,
          error: errMsg,
          healthy: false
        });
      }
    }

    const overallHealth = healthyCount === endpoints.length ? 'healthy' :
                          healthyCount > 0 ? 'degraded' : 'error';

    return {
      component: 'API Endpoints',
      status: overallHealth,
      responseTime: Date.now() - startTime,
      message: `${healthyCount}/${endpoints.length} endpoints responding`,
      details: results
    };
  }

  // 5. CORS ÏÑ§Ï†ï Ï†êÍ≤Ä
  async checkCORS(): Promise<HealthCheckResult> {
    const startTime = Date.now();
    try {
      const response = await axios.options(`${API_BASE_URL}/api/service/status/`, {
        timeout: 5000,
        validateStatus: () => true
      });

      const headers = response.headers;
      const corsHeaders = {
        'access-control-allow-origin': headers['access-control-allow-origin'],
        'access-control-allow-methods': headers['access-control-allow-methods'],
        'access-control-allow-headers': headers['access-control-allow-headers']
      };

      const hasProperCORS = corsHeaders['access-control-allow-origin'] !== undefined;

      return {
        component: 'CORS Configuration',
        status: hasProperCORS ? 'healthy' : 'error',
        responseTime: Date.now() - startTime,
        message: hasProperCORS ? 'CORS properly configured' : 'CORS not configured',
        details: corsHeaders
      };
    } catch (error: unknown) {
      const errMsg = error instanceof Error ? error.message : String(error);
      return {
        component: 'CORS Configuration',
        status: 'error',
        message: `CORS check failed: ${errMsg}`
      };
    }
  }

  // 6. Ïù∏Ï¶ù ÏãúÏä§ÌÖú Ï†êÍ≤Ä
  async checkAuthentication(): Promise<HealthCheckResult> {
    const startTime = Date.now();
    try {
      // ÌÜ†ÌÅ∞ Í≤ÄÏ¶ù ÏóîÎìúÌè¨Ïù∏Ìä∏ ÌÖåÏä§Ìä∏
      const response = await axios.post(
        `${API_BASE_URL}/api/auth/token/verify/`,
        { token: 'test-token' },
        {
          timeout: 5000,
          validateStatus: () => true
        }
      );

      // 401ÏùÄ Ï†ïÏÉÅ (ÏûòÎ™ªÎêú ÌÜ†ÌÅ∞), 500ÎåÄ ÏóêÎü¨Îäî Î¨∏Ï†ú
      const isHealthy = response.status < 500;

      return {
        component: 'Authentication System',
        status: isHealthy ? 'healthy' : 'error',
        responseTime: Date.now() - startTime,
        message: isHealthy ? 'Auth system is operational' : 'Auth system error',
        details: {
          endpoint: '/api/auth/token/verify/',
          status: response.status
        }
      };
    } catch (error: unknown) {
      const errMsg = error instanceof Error ? error.message : String(error);
      return {
        component: 'Authentication System',
        status: 'error',
        message: `Auth check failed: ${errMsg}`
      };
    }
  }

  // Ï†ÑÏ≤¥ ÏãúÏä§ÌÖú Ï†êÍ≤Ä Ïã§Ìñâ
  async runFullHealthCheck(): Promise<{
    timestamp: string;
    overallStatus: 'healthy' | 'degraded' | 'error';
    results: HealthCheckResult[];
    summary: {
      total: number;
      healthy: number;
      degraded: number;
      error: number;
    };
  }> {
    console.log('üîç Starting system health check...');
    
    this.results = [];

    // Î™®Îì† Ï†êÍ≤Ä Ïã§Ìñâ
    const checks = [
      this.checkFrontend(),
      this.checkBackendServer(),
      this.checkDatabase(),
      this.checkAPIEndpoints(),
      this.checkCORS(),
      this.checkAuthentication()
    ];

    const results = await Promise.all(checks);
    this.results = results;

    // Í≤∞Í≥º ÏßëÍ≥Ñ
    const summary = {
      total: results.length,
      healthy: results.filter(r => r.status === 'healthy').length,
      degraded: results.filter(r => r.status === 'degraded').length,
      error: results.filter(r => r.status === 'error').length
    };

    // Ï†ÑÏ≤¥ ÏÉÅÌÉú Í≤∞Ï†ï
    let overallStatus: 'healthy' | 'degraded' | 'error';
    if (summary.error > 0) {
      overallStatus = 'error';
    } else if (summary.degraded > 0) {
      overallStatus = 'degraded';
    } else {
      overallStatus = 'healthy';
    }

    const report = {
      timestamp: new Date().toISOString(),
      overallStatus,
      results,
      summary
    };

    // ÏΩòÏÜîÏóê Í≤∞Í≥º Ï∂úÎ†•
    this.printReport(report);

    return report;
  }

  // Î≥¥Í≥†ÏÑú Ï∂úÎ†•
  private printReport(report: HealthReport): void {
    console.log('\n' + '='.repeat(60));
    console.log('üè• SYSTEM HEALTH CHECK REPORT');
    console.log('='.repeat(60));
    console.log(`üìÖ Timestamp: ${report.timestamp}`);
    console.log(`üìä Overall Status: ${this.getStatusEmoji(report.overallStatus)} ${report.overallStatus.toUpperCase()}`);
    console.log('\nüìà Summary:');
    console.log(`  Total Checks: ${report.summary.total}`);
    console.log(`  ‚úÖ Healthy: ${report.summary.healthy}`);
    console.log(`  ‚ö†Ô∏è  Degraded: ${report.summary.degraded}`);
    console.log(`  ‚ùå Error: ${report.summary.error}`);
    console.log('\nüìã Detailed Results:');
    console.log('-'.repeat(60));
    
    report.results.forEach((result: HealthCheckResult) => {
      console.log(`\n${this.getStatusEmoji(result.status)} ${result.component}`);
      console.log(`  Status: ${result.status}`);
      if (result.responseTime) {
        console.log(`  Response Time: ${result.responseTime}ms`);
      }
      if (result.message) {
        console.log(`  Message: ${result.message}`);
      }
      if (result.details) {
        console.log(`  Details:`, result.details);
      }
    });
    
    console.log('\n' + '='.repeat(60));
  }

  private getStatusEmoji(status: string): string {
    switch (status) {
      case 'healthy': return '‚úÖ';
      case 'degraded': return '‚ö†Ô∏è';
      case 'error': return '‚ùå';
      default: return '‚ùì';
    }
  }
}

// Export singleton instance
const healthChecker = new SystemHealthChecker();

export default healthChecker;

// Export for direct use
export const runSystemHealthCheck = () => healthChecker.runFullHealthCheck();